@import url(https://fonts.googleapis.com/css2?family=Prompt:ital,wght@0,300;0,400;0,500;0,700;1,300;1,400;1,500;1,700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Prompt:ital,wght@0,300;0,400;0,500;0,700;1,300;1,400;1,500;1,700&display=swap);
.popup {
  top: 0;
  background-color: rgba(34, 34, 34, 0.5);
  -webkit-backdrop-filter: blur(10px);
  backdrop-filter: blur(10px);
  position: fixed;
  z-index: 1;
  width: 100%;
  height: 100%;
}

.fa-lock {
  color: rgb(114, 114, 114);
  margin-left: 20px;
  -webkit-transform: translateY(-2px);
          transform: translateY(-2px);
  font-size: .8em;
}

.navbar {
  z-index: 2;
  background-color: transparent !important;
}

.navbar .navbar-brand {
  color: rgb(218, 218, 218);
  font-family: 'Prompt', sans-serif;
  font-weight: 500;
  font-size: 1.1em;
}

.navbar .navbar-brand:hover {
  color: rgb(170, 170, 170);
}

.title {
  font-family: 'Prompt', sans-serif;
  font-weight: 700;
  font-size: 2.8em;
  letter-spacing: 0.01em !important;
  font-weight: 600;
}

.title-md {
  font-family: 'Prompt', sans-serif;
  font-weight: 700;
  font-size: 2em;
  letter-spacing: 0.01em !important;
  font-weight: 600;
}

.subtitle {
  font-family: "Prompt", sans-serif;
  letter-spacing: -0.02em !important;
  color: #a3a3a3;
}

.bot-card {
  padding: 20px;
  border: 2px solid;
  border-color: #0c0c0c;
  background-color: #242525;
  box-shadow: inset 0 2px 2px #333333;
  -webkit-box-shadow: inset 0 2px 2px #333333;
  -moz-box-shadow: inset 0 2px 2px #333333;
  border-radius: 6px;
}

.bot-card:hover {
  cursor: pointer;
  -webkit-transform: translateY(-3px);
          transform: translateY(-3px);
  border-color: #555757;
  box-shadow: none;
  -webkit-box-shadow: none;
  -moz-box-shadow: none;
}

.card-img {
  transition: all 200ms ease;
  border-radius: 22px;
}

.bot-card:hover .card-img {
  -webkit-transform: translateY(-5px);
          transform: translateY(-5px);
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.2);
}

.bot-card a {
  text-decoration: none !important;
}

.card-img {
  width: 100px;
  height: 100px;
}

.card-title {
  font-family: 'Prompt', sans-serif;
  font-weight: 500;
}

.card-subtitle {
  font-size: .8em;
  color: rgb(138, 138, 138);
}

/* ROOM */

.chat-input-container {
  position: fixed;
  bottom: 20px;
  padding-left: 20px;
  padding-right: 20px;
}

.chat-input-container>* {
  padding: 10px;
}

.send-button {
  background-color: rgb(61, 61, 61);
  border-color: #444444;
}

.send-button:hover {
  background-color: #444444 !important;
}

.send-button:hover>i {
  color: #9eb8ff !important;
}

.send-button>i {
  color: #a3a3a3;
}

/* Container of all div messages */

.messages {
  overflow-y: scroll;
  -ms-overflow-style: none;
  /* Internet Explorer 10+ */
  scrollbar-width: none;
  /* Firefox */
}

.messages::-webkit-scrollbar {
  display: none;
  /* Safari and Chrome */
}

/* Container of the text and user */

.message {
  padding: 5px 10px 5px 10px;
  width: auto;
  max-width: 100%;
  border-radius: 20px;
}

.message-text {
  word-wrap: break-word;
  line-height: 20px;
}

.message-from-me {
  background-color: #1982FC;
}

.message-from-user {
  background-color: #333333;
}

.message-sender {
  color: #555757;
  font-size: .8em;
  position: relative;
}
body {
    background-color: #1a191a;
    margin: 0;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}

input {
    border: 1px;
    border-color: #444444 !important;
    color: #fff !important;
    background-color: rgb(41, 41, 41) !important;
}

input:focus {
    background-color: rgb(37, 37, 37) !important;
}

input:disabled {
    background-color: rgb(20, 20, 20) !important;
}

input::-webkit-input-placeholder {
    color: rgb(92, 92, 92) !important;
}

input:-ms-input-placeholder {
    color: rgb(92, 92, 92) !important;
}

input::placeholder {
    color: rgb(92, 92, 92) !important;
}

.form-control {
    border: 0 !important;
}

h1, h2, h3, h4, h5, h6, p, span, label {
    color: #ffffff;
}

/* GLOBAL CLASSES */

.error-top {
    position: relative;
    z-index: 3;
    width: 100%;
    background-color: rgb(255, 89, 89);
}

.error-top p {
    padding: 5px;
    font-family: "Prompt", sans-serif;
    font-weight: 400;
}

.tr-f {
    transition: all 200ms ease;
}

.btn {
    background-color: rgb(13, 102, 208);
    color: #fff;
    font-weight: 600;
    border-radius: 5px;
}

.btn:hover {
    background-color: rgb(11, 86, 177);
    color: #fff;
}

.button-md {
    font-size: 1em;
}

.button-sm {
    font-size: .8em;
}
